.Modal{
   position:fixed; 
   z-index: 500;
   background-color:white;
   width: 70%;
   border: 1px solid #ccc;
   box-shadow: 1px 1px 1px black;
   padding: 16px;
   left: 30%;
   top: 30%;
   transition: all 0.7s ease-out;
}
@media (min-width : 500px){
    .Modal{
        width: 500px;
        left:calc(50%-250px);
    }
}